                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
              <v-dialog v-model="dialogDelete" max-width="500px">
                <v-card>
                  <v-card-title class="text-h5"
                    >Deseja mesmo excluir essa região?</v-card-title
                  >
                  <v-card-text v-if="periodsWithSelectedRegion.size > 1">
