                  </v-data-table>
                </v-col>
              </v-row>
            </v-card>
          </div>
          <v-dialog v-model="rowDialog" max-width="500px">
            <v-card>
              <v-card-title>
                <span class="text-h5">{{ groupRowDialogTitle }}</span>
              </v-card-title>

              <v-card-text>
                <v-container>
                  <v-form @submit.prevent ref="groupRowForm">
                    <v-row>
                      <v-col cols="12" sm="6" md="4">
