              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeGroupRow">
                  Cancelar
                </v-btn>
                <v-btn color="blue darken-1" text @click="saveGroupRow">
                  OK
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-dialog v-model="rowDialogDelete" max-width="600px">
            <v-card>
              <v-card-title class="text-h5"
                >Deseja mesmo remover esse professor do grupo?</v-card-title
              >
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeGroupRowDelete"
                  >Cancelar</v-btn
                >
                <v-btn color="blue darken-1" text @click="deleteGroupRowConfirm"
                  >OK</v-btn
                >
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-dialog v-model="groupDialogDelete" max-width="600px">
            <v-card>
              <v-card-title class="text-h5"
                >Deseja mesmo excluir esse grupo?</v-card-title
              >
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="closeDeleteGroupDialog"
                  >Cancelar</v-btn
                >
                <v-btn color="blue darken-1" text @click="deleteGroupConfirm"
                  >OK</v-btn
                >
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-dialog v-model="editClassDialog" max-width="500px">
            <v-card>
              <v-card-title>
                <span class="text-h5">Selecionar sala</span>
              </v-card-title>

              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col>
                      <v-select
                        v-model="editedGroupClass"
                        label="Sala"
                        item-text="name"
                        item-value="id"
                        :items="salas"
                        :rules="[rules.required]"
                      ></v-select>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeEditClassDialog">
                  Cancelar
                </v-btn>
                <v-btn color="blue darken-1" text @click="saveGroupClass">
                  OK
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-container>
      </v-col>
    </v-row>
  </v-card>
</template>

<script>
import helper from "@/mixins/helper";
import validationMixin from "@/mixins/validation";
import { v4 as uuidv4 } from "uuid";

export default {
  name: "Especiais",
  props: ["id"],
  mixins: [helper, validationMixin],

  data: function () {
    return {
      regions: [],
      regionPositions: {},
      editedIndex: -1,
      selectedRegion: null,
      selectedTeacher: null,
      editedItem: {
        id_professor: null,
        num_aulas: 0,
        regra: 0,
        movel: 0,
      },
      defaultItem: {
        id_professor: null,
        num_aulas: 0,
        regra: 0,
        movel: 0,
      },
      dialog: false,
      dialogDelete: false,
      dialogClearRegion: false,
      regionHeaders: [
        { text: "Professor", value: "id_professor" },
        { text: "Regra", value: "regra" },
        { text: "Aulas", value: "num_aulas" },
        // { text: "Móvel", value: "dia" },
        { text: "Ações", value: "actions" },
      ],
      rightMouseDown: 0,
      leftMouseDown: 0,
      startRow: null,
      startCol: null,
      endRow: null,
      endCol: null,
      lastSelection: [],
      deleteMode: false,
      turnos: [],
      professores: [],
      salas: [],
      opcoesRegras: [
        { text: "Exatamente", id: 0 },
        { text: "No mínimo", id: 1 },
        { text: "No máximo", id: 2 },
      ],
      professorAnterior: null,
      newGroupDialog: false,
      editClassDialog: false,
      groupHeaders: [
        { text: "Professor", value: "professor" },
        { text: "Aulas", value: "aulas" },
        { text: "Ações", value: "actions", sortable: false, width: "30%" },
      ],
      grupos: {},

      rowDialog: false,
      rowDialogDelete: false,
      editedGroupIndex: null,
      editedClass: null,
      editedRowIndex: null,
      editedGroupRow: { professor: null, aulas: 0 },
      defaultGroupRow: { professor: null, aulas: 0 },
      gruposReload: 0,

      editedGroupClass: null,
      groupDialogDelete: false,
    };
  },

  mounted() {
    this.load();
    if (this.$refs.leftTable) {
      const tbody =
        this.$refs.leftTable.$children[0].$children[0].$el.children[1]
          .children[0].children[2];

      tbody.addEventListener("mouseleave", () => {
        this.mouseUpHandler();
        this.clearLastSelection();
      });
    }
  },

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Nova região" : "Editar região";
    },
    groupRowDialogTitle() {
      return this.editedRowIndex !== -1
        ? "Editar professor no grupo"
        : "Adicionar professor ao grupo";
    },
    regionTitle() {
      if (this.selectedRegion == null) {
        return "Regiões";
      }

      const region = this.regions.find(
        (region) =>
          region.id_regiao === this.selectedRegion ||
          (region.id_regiao_temporario != null &&
            region.id_regiao_temporario === this.selectedRegion)
      );
      if (region == null || region.id_professor == null) {
        return "Regiões";
      }

      const teacher = this.professoresLookup[region.id_professor];
      const regra = this.opcoesRegras[region.regra].text;
      const { num_aulas } = region;
      let title = `Região (${teacher} - ${regra} ${num_aulas} aulas)`;

      if (this.periodsWithSelectedRegion.size > 1) {
        title += ` (${this.periodsWithSelectedRegion.size} turnos)`;
      }
      return title;
    },
    periodsWithSelectedRegion() {
      return this.getPeriodsWithRegion(this.selectedRegion);
    },
    regionPositionsTableReady() {
      return (
        this.selectedTeacher != null &&
        this.turnoSelecionado != null &&
        this.regionPositions[this.turnoSelecionado] != null &&
        this.regionPositions[this.turnoSelecionado][this.selectedTeacher] !=
          null
      );
    },
    regrasLookup() {
      const arr = Array.from(this.opcoesRegras, (opcao) => [
        opcao.id,
        opcao.text,
      ]);
      return Object.fromEntries(arr);
    },
    aulasPorDia() {
      return this.turnos.find((t) => t.id === this.turnoSelecionado).classes;
    },
  },

  watch: {
    dialog(val) {
      val || this.close();

      if (val === true) {
        this.resetValidation("regionForm");
      }
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
    dialogClearRegion(val) {
      val || this.closeDialogClearRegion();
    },
    newGroupDialog(val) {
      if (val === true) {
        this.resetValidation("groupForm");
      }
    },
    rowDialog(val) {
      if (val === true) {
        this.resetValidation("groupRowForm");
      }
    },
  },

  methods: {
    changeTeacher(e, item) {
      this.professorAnterior = item.id_professor;
      item.id_professor = e;
    },
    mouseDownHandler(e, row, col) {
      if (this.selectedRegion == null) {
        return this.$toast.warning(
          "Para definir as posições, selecione ou crie uma região"
        );
      }

      if (e.button === 0) {
        this.leftMouseDown = 1;
      } else {
        this.rightMouseDown = 1;
      }
      this.startRow = row;
      this.startCol = col;
    },

    mouseUpHandler(e, row, col) {
      this.leftMouseDown = 0;
      this.rightMouseDown = 0;
      this.endRow = row;
      this.endCol = col;
      this.lastSelection = [];
    },

    fillRegion(startRow, endRow, startCol, endCol) {
      const startR = Math.min(startRow, endRow);
      const endR = Math.max(startRow, endRow);
      const startC = Math.min(startCol, endCol);
      const endC = Math.max(startCol, endCol);
      this.clearLastSelection();
      this.lastSelection = [];
      for (let i = startR; i <= endR; i++) {
        for (let j = startC; j <= endC; j++) {
          if (
            !this.regionPositions[this.turnoSelecionado][this.selectedTeacher][
              i
            ][this.headersSalasTurnoSelecionado[j].value].includes(
              this.selectedRegion
            ) &&
            this.leftMouseDown
          ) {
            const index = this.sortedIndex(
              this.regionPositions[this.turnoSelecionado][this.selectedTeacher][
                i
              ][this.headersSalasTurnoSelecionado[j].value],
              this.selectedRegion
            );
            this.regionPositions[this.turnoSelecionado][this.selectedTeacher][
              i
            ][this.headersSalasTurnoSelecionado[j].value].splice(
              index,
              0,
              this.selectedRegion
            );
            this.lastSelection.push([
              i,
              this.headersSalasTurnoSelecionado[j].value,
              this.regionPositions[this.turnoSelecionado][this.selectedTeacher][
                i
              ][this.headersSalasTurnoSelecionado[j].value].filter(
                (v) => v !== this.selectedRegion
              ),
            ]);
          } else if (
            this.regionPositions[this.turnoSelecionado][this.selectedTeacher][
              i
            ][this.headersSalasTurnoSelecionado[j].value].includes(
              this.selectedRegion
            ) &&
            this.rightMouseDown
          ) {
            this.lastSelection.push([
              i,
              this.headersSalasTurnoSelecionado[j].value,
              this.regionPositions[this.turnoSelecionado][this.selectedTeacher][
                i
              ][this.headersSalasTurnoSelecionado[j].value],
            ]);

            this.removeItemOnce(
              this.regionPositions[this.turnoSelecionado][this.selectedTeacher][
                i
              ][this.headersSalasTurnoSelecionado[j].value],
              this.selectedRegion
            );
          }
        }
      }
    },

    removeItemOnce(arr, value) {
      var index = arr.indexOf(value);
      if (index > -1) {
        arr.splice(index, 1);
      }
    },

    sortedIndex(array, value) {
      var low = 0,
        high = array.length;

      while (low < high) {
        var mid = (low + high) >>> 1;
        if (array[mid] < value) low = mid + 1;
        else high = mid;
      }
      return low;
    },

    clearLastSelection() {
      this.lastSelection.forEach((pos) => {
        this.regionPositions[this.turnoSelecionado][this.selectedTeacher][
          pos[0]
        ][pos[1]] = pos[2] || [];
      });
    },

    editItem(item) {
      this.editedIndex = this.regions.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteRegion(item) {
      this.editedIndex = this.regions.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    clearRegionPositions(regionId) {
      const reg = this.getRegion(regionId);
      if (reg == null) {
        return;
      }

      const { id_professor, id_regiao, id_regiao_temporario } = reg;

      for (const turno of this.turnos) {
        if (this.regionPositions[turno.id] == null) {
          continue;
        }
        for (let i = 0; i < this.numero_dias * turno.classes; i++) {
          for (const sala of this.getClassesFromPeriod(turno.id)) {
            let position =
              this.regionPositions[turno.id][id_professor][i][sala.id];
            const filteredPosition = position.filter(
              (id) =>
                id !== id_regiao &&
                (id_regiao_temporario == null || id !== id_regiao_temporario)
            );
            this.regionPositions[turno.id][id_professor][i][sala.id] =
              filteredPosition;
          }
        }
      }
      this.dialogClearRegion = false;
    },

    deleteRegionConfirm() {
      const { id_professor, id_regiao, id_regiao_temporario } =
        this.regions[this.editedIndex];

      this.clearRegionPositions(id_regiao || id_regiao_temporario);
      this.regions.splice(this.editedIndex, 1);

      if (
        this.selectedRegion === id_regiao ||
        (id_regiao_temporario != null &&
          this.selectedRegion === id_regiao_temporario)
      ) {
        const nextRegion = this.regions.find(
          (region) => region.id_professor === id_professor
        );
        if (nextRegion) {
          this.selectedRegion =
            nextRegion.id_regiao || nextRegion.id_regiao_temporario;
          this.selectedTeacher = nextRegion.id_professor;
        } else if (this.regions.length > 0) {
          const region = this.regions[0];
          this.selectedRegion = region.id_regiao || region.id_regiao_temporario;
          this.selectedTeacher = region.id_professor;
        } else {
          this.selectedRegion = null;
        }
      }
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDialogClearRegion() {
      this.dialogClearRegion = false;
    },

    saveRegion() {
      if (!this.validate("regionForm")) {
        return;
      }

      if (this.editedIndex > -1) {
        this.checkChanges(this.regions[this.editedIndex], this.editedItem);
        Object.assign(this.regions[this.editedIndex], this.editedItem);
        //tranferir região para o outro professor
        const { id_professor } = this.editedItem;
        // for (const turno of this.turnos) {
        //   for (let i = 0; i < this.numero_dias * turno.classes; i++) {
        //     for (const sala of this.salas) {
        //       if (
        //         this.regionPositions[turno.id][this.professorAnterior][i][
        //           sala.id
        //         ] === id_regiao ||
        //         this.regionPositions[turno.id][this.professorAnterior][i][
        //           sala.id
        //         ] === id_regiao_temporario
        //       ) {
        //         this.regionPositions[turno.id][id_professor][i][sala.id] =
        //           this.regionPositions[turno.id][this.professorAnterior][i][
        //             sala.id
        //           ];
        //         this.regionPositions[turno.id][this.professorAnterior][i][
        //           sala.id
        //         ] = [];
        //       }
        //     }
        //   }
        // }
        this.selectedTeacher = id_professor;
      } else {
        this.changed();
        this.editedItem.id_regiao_temporario = uuidv4();
        this.regions.push(this.editedItem);
        this.selectedRegion = this.editedItem.id_regiao_temporario;
        this.selectedTeacher = this.editedItem.id_professor;
      }
      this.close();
    },

    getClassIndex(classId) {
      return this.headersSalasTurnoSelecionado.findIndex(
        (h) => h.value === classId
      );
    },

    viewItem(item) {
      this.selectedRegion = item.id_regiao || item.id_regiao_temporario;
      this.selectedTeacher = item.id_professor;
    },

    closeNewGroupDialog() {
      this.newGroupDialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    saveGroup() {
      if (!this.validate("groupForm")) {
        return;
      }

      if (this.editedItem.sala1 == null || this.editedItem.sala2 == null) {
        return this.$toast.error(
          "É obrigatório configurar ambas as salas para criar o grupo de alinhamento"
        );
      }

      if (this.editedItem.sala1 === this.editedItem.sala2) {
        return this.$toast.error(
          "O grupo de alinhamento deve relacionar salas diferentes"
        );
      }

      this.changed();
      if (!Object.hasOwn(this.grupos, this.turnoSelecionado)) {
        this.grupos[this.turnoSelecionado] = [];
      }

      this.grupos[this.turnoSelecionado].push({
        id: null,
        salas: [
          { id: this.editedItem.sala1, professores: [] },
          { id: this.editedItem.sala2, professores: [] },
        ],
      });
      this.closeNewGroupDialog();
    },
    deleteGroup(groupIndex) {
      this.editedGroupIndex = groupIndex;
      this.groupDialogDelete = true;
    },
    deleteGroupConfirm() {
      this.grupos[this.turnoSelecionado].splice(this.editedGroupIndex, 1);
      this.closeDeleteGroupDialog();
      this.changed();
      this.$nextTick(() => {
        this.editedGroupIndex = null;
      });
    },
    closeDeleteGroupDialog() {
      this.groupDialogDelete = false;
    },
    validateGroups() {
      let valid = true;
      for (const turnoId in this.grupos) {
        this.grupos[turnoId].forEach((group) => {
          const totais = [];
          group.salas.forEach((sala) => {
            const totalAulas = sala.professores.reduce((sum, row) => {
              return sum + Number(row.aulas);
            }, 0);
            totais.push(totalAulas);
          });
          const wrong = totais.some((v) => v !== totais[0]);
          if (wrong) {
            valid = false;
          }
        });
      }
      return valid;
    },
    validateNoEmptyGroups() {
      let valid = true;
      for (const turnoId in this.grupos) {
        this.grupos[turnoId].forEach((group) => {
          group.salas.forEach((sala) => {
            if (sala.professores.length === 0) {
              valid = false;
            }
          });
        });
      }
      return valid;
    },

    async save(reload = true) {
      if (!this.validateGroups()) {
        this.$toast.error(
          "Cada sala do grupo de alinhamento deve ter a mesma quantidade de aulas a serem alinhadas"
        );
        return;
      }

      if (!this.validateNoEmptyGroups()) {
        this.$toast.error(
          "Não é possível salvar um grupo sem professores inseridos"
        );
        return;
      }

      if (!this.validateMobileRegions()) {
        this.$toast.error(
          "Regiões móveis devem estar contidas em um único dia de um único turno."
        );
        return;
      }

      const resp = await this.$axios.post("/especiais/", {
        regioes: this.regions,
        posicoesRegioes: this.regionPositions,
        gruposAlinhamento: this.grupos,
        id: this.id,
      });

      if (resp.status === 200 && reload) {
        this.load(true);
      }
    },

    async load(isReload = false) {
      try {
        this.setLoading(isReload);
        this.saved();
        await this.getStructure();
        const resp = await this.$axios.get("/especiais/" + this.id);
        const { regioes, gruposAlinhamento, posicoesRegioes } = resp.data;
        this.regions = regioes;
        this.grupos = gruposAlinhamento;

        const posicoes = {};

        for (const turno of this.turnos) {
          posicoes[turno.id] = {};
          for (const professor of this.professores) {
            posicoes[turno.id][professor.id] = [];
            for (let i = 0; i < this.numero_dias * turno.classes; i++) {
              const linha = { aula: i };
              for (const sala of this.getClassesFromPeriod(turno.id)) {
                if (sala.turno === turno.id) {
                  linha[sala.id] = [];
                }
              }
              posicoes[turno.id][professor.id].push(linha);
            }
          }
        }

        for (const linha of posicoesRegioes) {
          const { id_regiao, id_sala, id_professor, aula } = linha;
          for (const turno of this.turnos) {
            if (id_sala in posicoes[turno.id][id_professor][aula]) {
              const index = this.sortedIndex(
                posicoes[turno.id][id_professor][aula][id_sala],
                id_regiao
              );
              posicoes[turno.id][id_professor][aula][id_sala][index] =
                id_regiao;
            }
          }
        }

        const grupos = {};
        for (const turno of this.turnos) {
          grupos[turno.id] = [];
        }

        for (const grupo of gruposAlinhamento) {
          const grupo_formatado = { salas: [] };
          for (const salaId of grupo.salas_grupo) {
            grupo_formatado.salas.push({ id: salaId, professores: [] });
          }
          for (let i = 0; i < grupo.professor.length; i++) {
            const indexSala = grupo.salas_grupo.findIndex(
              (s) => s === grupo.sala[i]
            );
            if (indexSala !== -1) {
              grupo_formatado.salas[indexSala].professores.push({
                professor: grupo.professor[i],
                aulas: grupo.aula[i],
              });
            }
          }
          grupos[grupo.id_turno].push(grupo_formatado);
        }
        this.grupos = grupos;
        this.regionPositions = posicoes;

        if (this.regions.length === 0) {
          if (this.professores.length > 0) {
            this.selectedTeacher = this.professores[0].id;
          }
        } else if (
          this.selectedRegion == null ||
          this.getRegion(this.selectedRegion) == null
        ) {
          this.selectedRegion = this.regions[0].id_regiao;
          this.selectedTeacher = this.regions[0].id_professor;
        }
      } catch (e) {
        console.error(e);
      } finally {
        this.clearLoading();
      }
    },

    item_classes(item) {
      if (
        item.aula % this.aulasPorDia == this.aulasPorDia - 1 &&
        item.aula !== this.aulasPorDia * this.numero_dias - 1
      ) {
        return "ultima";
      }
    },

    getRegion(regionId) {
      return this.regions.find((region) => {
        return [region.id_regiao, region.id_regiao_temporario].includes(
          regionId
        );
      });
    },

    paintCell(e, item, salaId, row) {
      if (
        this.selectedRegion != null &&
        this.selectedTeacher ===
          this.getRegion(this.selectedRegion).id_professor &&
        (this.leftMouseDown || this.rightMouseDown)
      ) {
        this.changed();
        this.fillRegion(
          this.startRow,
          row,
          this.getClassIndex(this.startCol),
          this.getClassIndex(salaId)
        );
        return;
      }
    },

    preventDefault(e) {
      e.preventDefault();
    },

    getColor(region) {
      const highlightColor = "#03a9f4";
      const secondaryColor = "#01579b";
      if (region.length === 0) {
        return null;
      } else if (region.includes(this.selectedRegion)) {
        return highlightColor;
      }
      return secondaryColor;
    },

    getPeriodsWithRegion(regionId) {
      const region = this.getRegion(regionId);
      if (region == null) {
        return false;
      }

      const periodsWithRegion = new Set();
      for (const turno of this.turnos) {
        for (const professor of this.professores) {
          if (this.regionPositions[turno.id] == null) {
            continue;
          }
          for (const linha of this.regionPositions[turno.id][professor.id]) {
            for (const sala of this.getClassesFromPeriod(turno.id)) {
              if (linha[sala.id].includes(regionId)) {
                periodsWithRegion.add(turno.id);
              }
            }
          }
        }
      }
      return periodsWithRegion;
    },

    validateMobileRegions() {
      const daysPerRegion = {};
      for (const turno of this.turnos) {
        for (const professor of this.professores) {
          if (this.regionPositions[turno.id] == null) {
            continue;
          }
          for (const linha of this.regionPositions[turno.id][professor.id]) {
            for (const sala of this.getClassesFromPeriod(turno.id)) {
              for (const region of linha[sala.id]) {
                if (!(region in daysPerRegion)) {
                  daysPerRegion[region] = new Set();
                }
                const day = Math.floor(linha.aula / turno.classes);
                const identifier = turno.id + "/" + day;
                daysPerRegion[region].add(identifier);
              }
            }
          }
        }
      }
      for (const region_id in daysPerRegion) {
        if (
          daysPerRegion[region_id].size > 1 &&
          this.regions.find(
            (r) =>
              r.id_regiao === Number(region_id) ||
              (r.id_regiao_temporario != null &&
                r.id_regiao_temporario === region_id)
          ).movel
        ) {
          return false;
        }
      }
      return true;
    },

    editGroupRow(groupIndex, classIndex, rowIndex) {
      this.editedGroupIndex = groupIndex;
      this.editedClass = classIndex;
      this.editedRowIndex = rowIndex;
      this.rowDialog = true;

      if (rowIndex !== -1) {
        this.editedGroupRow = {
          ...this.getGroupRowObject(
            this.turnoSelecionado,
            groupIndex,
            classIndex,
            rowIndex
          ),
        };
      } else {
        this.editedGroupRow = Object.assign({}, this.defaultGroupRow);
      }
    },

    deleteGroupRow(groupIndex, classIndex, rowIndex) {
      this.editedGroupIndex = groupIndex;
      this.editedClass = classIndex;
      this.editedRowIndex = rowIndex;
      this.rowDialogDelete = true;
    },

    deleteGroupRowConfirm() {
      const group = this.grupos[this.turnoSelecionado][this.editedGroupIndex];
      const classObject = group.salas[this.editedClass];

      classObject.professores.splice(this.editedRowIndex, 1);
      this.closeGroupRowDelete();
      this.changed();
    },

    closeGroupRow() {
      this.rowDialog = false;
      this.$nextTick(() => {
        this.editedGroupIndex = null;
        this.editedClass = null;
        this.editedRowIndex = null;
        this.editedGroupRow = Object.assign({}, this.defaultGroupRow);
      });
    },

    closeGroupRowDelete() {
      this.rowDialogDelete = false;
    },

    saveGroupRow() {
      if (!this.validate("groupRowForm")) {
        return;
      }

      const group = this.grupos[this.turnoSelecionado][this.editedGroupIndex];
      const classObject = group.salas[this.editedClass];

      const professor = this.editedGroupRow.professor;

      const teacherAlreadyPresent = classObject.professores.some(
        (row, index) =>
          row.professor === professor && index !== this.editedRowIndex
      );

      if (teacherAlreadyPresent) {
        return this.$toast.error("Este professor já está alocado no grupo");
      }

      if (this.editedRowIndex !== -1) {
        this.checkChanges(
          classObject.professores[this.editedRowIndex],
          this.editedGroupRow
        );
        classObject.professores[this.editedRowIndex] = Object.assign(
          {},
          this.editedGroupRow
        );
      } else {
        this.changed();
        classObject.professores.push(Object.assign({}, this.editedGroupRow));
      }

      this.closeGroupRow();
      this.gruposReload += 1;
    },

    getGroupRowObject(periodId, groupIndex, classIndex, rowIndex) {
      const group = this.grupos[periodId][groupIndex];
      const classObject = group.salas[classIndex];
      const teacherObject = classObject.professores[rowIndex];

      return Object.assign({}, teacherObject);
    },

    handleGroupRowTeacherChange(teacherId) {
      this.editedGroupRow.professor = teacherId;
    },

    editClass(groupIndex, classIndex) {
      this.editedGroupIndex = groupIndex;
      this.editedClass = classIndex;

      const group = this.grupos[this.turnoSelecionado][groupIndex];
      const classObject = group.salas[classIndex];

      this.editedGroupClass = classObject.id;
      this.editClassDialog = true;
    },

    closeEditClassDialog() {
      this.editClassDialog = false;
      this.$nextTick(() => {
        this.editedGroupIndex = null;
        this.editedClass = null;
        this.editedGroupClass = null;
      });
    },

    saveGroupClass() {
      const group = this.grupos[this.turnoSelecionado][this.editedGroupIndex];

      const selectedClass = this.editedGroupClass;

      if (
        group.salas.some(
          (sala, index) =>
            sala.id === selectedClass && index != this.editedClass
        )
      ) {
        return this.$toast.error("Esta sala já está alocada no grupo");
      }
      this.checkChanges(classObject.id, this.editedGroupClass.valueOf());
      const classObject = group.salas[this.editedClass];

      classObject.id = this.editedGroupClass.valueOf();
      this.gruposReload += 1;
      this.closeEditClassDialog();
    },
  },
};
</script>

